import { ProductsPageList } from './ProductsPageList'
import { useAppSelector } from '../app/hooks'
import { Pagination } from '../components/Pagination'
import { SizeList } from '../components/SizeList'
import { useEffect, useMemo, useState } from 'react'
import { useLocation, useSearchParams } from 'react-router-dom'
import { Arrow } from './ProductPage/Arrow'

type Sort = {
  [key: string]: any
}
const itemsOnPage = ['15', '9', '3', 'all']

const getTitle = (path: string) => {
  const sliceHomePage = path.slice(1)
  const index = sliceHomePage.indexOf('?')
  return sliceHomePage.slice(0, index)
}

export const ProductsPage = () => {
  const items: any[] = useAppSelector((state) => state.productList.products)
  const [item, setItem] = useState(itemsOnPage[0])
  const path = useLocation().pathname
  const [params, setParams] = useSearchParams()
  const perPage = params.get('perPage') || '15'
  const page = params.get('page') || '1'
  const paginationLength = Math.ceil(items.length / (perPage ? +perPage : 15))
  const [sortBy, setSortBy] = useState<null | boolean>(null)

  const sort: Sort = {
    null: {
      className: '--up',
      list: [...items],
    },

    true: {
      className: '--down',
      list: [...items].sort((a, b) => b.price - a.price),
    },

    false: {
      className: '--off',
      list: [...items].sort((a, b) => a.price - b.price),
    },
  }

  const sliceProductList = (list: any[], page: string, perPage: string) => {
    if (perPage === 'all') {
      return list
    }

    const from = +page * +perPage - +perPage
    const to = from + +perPage

    return list.slice(from, to)
  }

  useEffect(() => {
    if (perPage) {
      setItem(perPage)
    }
  }, [])

  useEffect(() => {
    params.set('perPage', item)

    setParams(params)
  }, [item])

  useEffect(() => {
    if (perPage === '15') {
      params.delete('perPage')
    }
    setParams(params)
  }, [perPage])

  const handleSort = (sortBy: null | boolean) => {
    if (sortBy === null) {
      setSortBy(true)
      return
    }
    if (sortBy) {
      setSortBy(false)
      return
    }

    setSortBy(null)
  }

  const sortObj = sort[String(sortBy)]
  const listToRender = sliceProductList(sortObj.list, page, perPage)

  return (
    <main className="products-page">
      <h1 className="products-page__title">
        {(getTitle(path) + 's').toUpperCase()}
      </h1>

      <div className="products-page__control">
        <div style={{ fontWeight: 200 }}>
          ITEMS ON PAGE:{' '}
          <SizeList sizes={itemsOnPage} size={item} setSize={setItem} />
        </div>

        <div style={{ fontWeight: 200 }}>
          SORT PRICE:{' '}
          <Arrow
            variant={sortObj.className}
            handleClick={() => handleSort(sortBy)}
          />
        </div>
      </div>
      <div className="products-page__content">
        <ProductsPageList items={listToRender} />
        {perPage !== 'all' && (
          <Pagination
            paginationLength={paginationLength || items.length / 15}
          />
        )}
      </div>
    </main>
  )
}
